header {
  font-size: clamp(rem(14px), 1.25vw, rem(18px));
  width: 100%;
  background: $clr1;
  position: sticky;
  top: 0;
  z-index: 15;
  backdrop-filter: blur(5px);
  font-family: $font-b;

  span {
    font-size: clamp(rem(20px), 2vw, rem(26px));
  }

  nav {
    margin: 0 auto;
    padding: 0 25px;
    position: relative;
    width: 100%;

    .burger-menu {
      display: block;

      @include for-normal-layout {
        display: none;
      }
    }

    .nav {
      gap: 60px;
      display: none;
      height: 100%;

      @include for-normal-layout {
        display: flex;
      }

      .tab {
        height: 100%;
        display: flex;
        align-items: center;
        a {
          position: relative;
          overflow: hidden;

          &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 0.1em;
            background-color: hotpink;
            opacity: 0;
            transition: opacity 300ms, transform 300ms;
            opacity: 1;
            transform: scale(0);
            transform-origin: center;
          }
          .active {
            opacity: 1;
            transform: translate3d(0, 0.2em, 0);
            transform: scale(1);
            // text-decoration: underline;
          }
        }

        &.projects-hover {
          &:hover {
            .dropdown-container {
              display: block;
            }
          }
        }

        .projects {
          height: 100%;
        }

        .dropdown-container {
          display: none;
          width: calc(100vw - 12px);
          height: 100%;
          position: fixed;
          bottom: calc(-100%);
          left: 0;
          background: $clr1;
          outline-width: 0 0 2px 0;
          .dropdown {
            height: 100%;
            margin: 0 auto;
            padding: 0px 25px;
            justify-content: flex-end;
            gap: 60px;
          }
        }
      }
    }
  }
}
